@use "global" as *;

.message {
    overflow: hidden;
}

.message__flex {
    display: flex;

    @include mq("md") {
        display: block;
    }
}

.message__content {
    width: 67.272727%;
    background-color:  $black;
    padding: rem(70) rem(30);

    @include mq("md") {
        width: 100vw;
        margin: 0 calc(50% - 50vw);
        padding: rem(60) rem(20) rem(246);

    }
}

.message__body {
    @include mq("md") {
        max-width: rem(600);
        width: 100%;
        padding: 0 rem(20);
        margin: 0 auto;
    }
}

.message__title {
    padding: 0 rem(2);
}

.message__text,
.message__ceo-name {
    font-size: rem(16);
    font-weight: $regular;

    @include mq("md") {
        font-size: rem(14);
    }
}

.message__text {
    margin-top: rem(60);
    line-height: lh(38, 16);

    @include mq("md") {
        line-height: lh(32, 14);
        margin-top: rem(30);
    }
}

.message__ceo-name {
    margin-top: rem(40);

    @include mq("md") {
        margin-top: rem(30);
    }
}

.message__career {
    margin-top: rem(20);
    color: $black;
}

.message__img {
    width: 32.7272%;
    aspect-ratio: 360/535;
    overflow: hidden;

    @include mq("md") {
        width: 100%;
        aspect-ratio: 335/300;
        margin-top: rem(-217.6);
    }
}

.message__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}